.todo-list {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  max-width: 420px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-shadow: 0px 14px 34px rgba(0, 0, 0, 0.25);
          box-shadow: 0px 14px 34px rgba(0, 0, 0, 0.25);
  border-radius: 20px 0px 40px;
  margin-top: 162px;
  margin-left: auto;
  margin-right: auto;
  padding: 30px;
  background-color: #252E42; }

.todo-list::before {
  position: absolute;
  content: "";
  width: 380.1px;
  height: 67.97px;
  left: 0;
  top: -20px;
  background: -webkit-gradient(linear, left top, left bottom, from(#31394D), to(#091739));
  background: linear-gradient(180deg, #31394D 0%, #091739 100%);
  border-radius: 20px;
  -webkit-transform: rotate(-2.32deg);
          transform: rotate(-2.32deg);
  z-index: -10;
  -webkit-transition: top 0.2s;
  transition: top 0.2s; }

.todo-list:hover::before {
  top: -10px;
  -webkit-transition: top 0.2s;
  transition: top 0.2s; }

.todo-list:hover::after {
  top: -20px;
  -webkit-transition: top 0.2s;
  transition: top 0.2s; }

.todo-list::after {
  position: absolute;
  content: "";
  width: 341.86px;
  height: 85.23px;
  left: 0px;
  top: -40px;
  background: -webkit-gradient(linear, left top, left bottom, from(#4F5565), color-stop(53.65%, #000000));
  background: linear-gradient(180deg, #4F5565 0%, #000000 53.65%);
  border-radius: 20px;
  -webkit-transform: rotate(-4.48deg);
          transform: rotate(-4.48deg);
  z-index: -20;
  -webkit-transition: top 0.2s;
  transition: top 0.2s; }

.todo-list__header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
