  height: 50px;
  right: 4px;
  bottom: 0;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(37, 46, 66, 0)), color-stop(67.19%, #252E42));
  background: linear-gradient(180deg, rgba(37, 46, 66, 0) 0%, #252E42 67.19%);
