.todo-list__list {
  position: relative;
  margin: 0;
  padding: 0;
  list-style: none;
  max-height: 430px;
  overflow: auto; }

.todo-list__list::before {
  position: absolute;
  content: "";
  width: 34px;
  height: 100%;
  right: 0;
  top: 0;
  background: -webkit-gradient(linear, right top, left top, color-stop(9.38%, #252E42), color-stop(66.15%, rgba(37, 46, 66, 0)));
  background: linear-gradient(270deg, #252E42 9.38%, rgba(37, 46, 66, 0) 66.15%);
