.todo-list__list li {
  position: relative;
  padding: 9px 0 9px 35px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

.todo-list__list label {
  cursor: pointer; }

.todo-list__list label::before {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background-color: #4f5565;
  border-radius: 6px; }

.todo-list__list input[type="checkbox"]:checked + label {
  text-decoration: line-through;
  color: #4f5565; }

.todo-list__list input[type="checkbox"]:hover + label,
.todo-list__list input[type="checkbox"]:hover + label span:nth-child(2) {
  color: #ff8469; }

.todo-list__list input[type="checkbox"]:checked:hover + label,
.todo-list__list input[type="checkbox"]:checked:hover + label span:nth-child(2) {
  color: #ffffff; }

.todo-list__list input[type="checkbox"]:checked + label::before {
  content: url("../img/checkmark.svg");
  background-color: #ff8469;
  text-align: center; }

.todo-list__list input[type="checkbox"]:hover + label::before {
  background-color: #ff8469;
  opacity: .2; }

.todo-list__list input[type="checkbox"]:checked + label::before {
  opacity: 1; }

.todo-list__list input[type="checkbox"] {
  position: absolute; }

.todo-list__list label span:first-child {
  display: block;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px; }

.todo-list__list label span:nth-child(2) {
